/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable @next/next/no-img-element */
import { useState, forwardRef, useCallback, useMemo } from "react";
import classNames from "classnames";
import { FaChevronDown, FaChevronUp } from "react-icons/fa";
import { Tooltip } from "react-tooltip";
import { useSession } from "next-auth/react";
import useWindowSize from "@/hooks/useWindowSize";
import { useQueryState } from "nuqs";
import { DISPLAY, Memberships } from "@/constants";
import useAxios from "@/hooks/useAxios";
import { Button } from "@nextui-org/react";
import arrowCreate, { DIRECTION } from "arrows-svg";

const NodeAvatar = forwardRef(
  (
    {
      id,
      name,
      email,
      avatar,
      x,
      y,
      size,
      onClick,
      high_membership,
      membership,
      membership_status,
      isSM,
    }: any,
    ref: any
  ) => {
    const formattedName = (name: string) => {
      if (!name) return "";
      return name.length > 15 ? name?.slice(0, 15) + ".." : name;
    };
    return (
      <>
        <div
          className={classNames(`absolute  rounded-lg py-4 px-4`, {
            "h-[180px]": !isSM,
            "h-[120px]": isSM,
            "hover:cursor-pointer  shadow-md border-gray-400": Boolean(name),
            "": !name,
          })}
          style={{
            left: x,
            top: y,
            boxShadow: "var(--tw-shadow-color) 0px 15px 20px -10px",
          }}
          id={id}
          ref={ref}
          onClick={onClick}
        >
          <img
            src="/img/logo2/logo-light-full.svg"
            className="absolute left-1/2 top-1/2 -translate-y-1/2 -translate-x-1/2 opacity-5 w-4/5"
            style={{ display: high_membership ? "block" : "none" }}
          />
          <div className="relative flex flex-col items-center space-y-4">
            <div
              className={classNames(
                "relative",
                name && "gradient-border",
                name && membership_status == "expired" && "before-border-red"
              )}
            >
              {name ? (
                <div
                  className={classNames(
                    "rounded-full flex items-center justify-center bg-[#f2f3f7] overflow-hidden",
                    {
                      "h-[150px] w-[150px]": !isSM,
                      "h-[90px] w-[90px]": isSM,
                    }
                  )}
                >
                  {avatar ? (
                    <img
                      src={avatar}
                      className={"w-full h-full object-cover"}
                      alt=""
                    />
                  ) : (
                    <img src="/img/nopeople.png" className="w-[30%]" alt="" />
                  )}
                </div>
              ) : (
                <div
                  className={classNames(
                    "rounded-full flex items-center justify-center bg-gray-800 overflow-hidden",
                    {
                      "h-[150px] w-[150px]": !isSM,
                      "h-[90px] w-[90px]": isSM,
                    }
                  )}
                >
                  <img src="/img/nopeople.png" className="w-[30%]" alt="" />
                </div>
              )}
            </div>
            {name && (
              <div>
                <span className="flex flex-col items-center">
                  <span className="text-xl font-bold whitespace-nowrap line-clamp-1 z-50">
                    {formattedName(name)}
                  </span>
                </span>
              </div>
            )}
          </div>
        </div>
        {name && (
          <Tooltip anchorSelect={"#" + id} place="left">
            <div className="flex flex-col">
              <span>
                <b>Email</b>
              </span>
              <span>{email}</span>
            </div>
            <div className="flex flex-col">
              <span>
                <b>Paquete</b>
              </span>
              <span>{DISPLAY[membership as Memberships]}</span>
            </div>
          </Tooltip>
        )}
      </>
    );
  }
);
NodeAvatar.displayName = "NodeAvatar";

export default function BinaryChart() {
  const [userID] = useQueryState("userID");
  const { data } = useSession();
  const [rootNodeId, setRootNodeId] = useState(userID ? userID : data!.user.id);
  const [leftPoints, setLeftPoints] = useState<any[]>([]);
  const [rightPoints, setRightPoints] = useState<any[]>([]);
  const [arrowsSetted, setArrows] = useState(false);
  const [openModal, setOpenModal] = useState<null | string>(null);
  const { width } = useWindowSize();

  const isSM = useMemo(() => width && width <= 1700, [width]);
  const { data: tree } = useAxios<any>({
    url: "binary/get-tree",
    method: "get",
  });

  const containerWidth = isSM ? 750 : 1200;

  const initPosition = isSM ? 300 : 700 - 150;

  const renderarrow = useCallback(() => {
    const head = {
      func: () => {
        return {
          node: '<rect x="-10" y="-10" width="20" height="25" style="display: none" />',
          width: 1,
          height: 1,
        };
      },
    };

    const wrapper = document.querySelector("#treeWrapper");

    const nodemain: HTMLElement = document.querySelector("#node-main")!;
    const left: HTMLElement = document.querySelector("#left")!;
    const right: HTMLElement = document.querySelector("#right")!;
    const left_left: HTMLElement = document.querySelector("#left-left")!;
    const left_right: HTMLElement = document.querySelector("#left-right")!;
    const right_left: HTMLElement = document.querySelector("#right-left")!;
    const right_right: HTMLElement = document.querySelector("#right-right")!;

    if (wrapper && nodemain && !arrowsSetted) {
      setArrows(true);
      const arrow1 = arrowCreate({
        head,
        from: {
          node: nodemain,
          direction: DIRECTION.LEFT,
        },
        to: {
          node: left,
          direction: DIRECTION.TOP,
        },
      });
      wrapper.appendChild(arrow1.node);

      const arrow2 = arrowCreate({
        head,
        from: {
          node: nodemain,
          direction: DIRECTION.RIGHT,
        },
        to: {
          node: right,
          direction: DIRECTION.TOP,
        },
      });
      wrapper.appendChild(arrow2.node);

      const arrow3 = arrowCreate({
        head,
        from: {
          node: left,
          direction: DIRECTION.BOTTOM,
        },
        to: {
          node: left_left,
          direction: DIRECTION.TOP,
        },
      });
      wrapper.appendChild(arrow3.node);

      const arrow4 = arrowCreate({
        head,
        from: {
          node: left,
          direction: DIRECTION.BOTTOM,
        },
        to: {
          node: left_right,
          direction: DIRECTION.TOP,
        },
      });
      wrapper.appendChild(arrow4.node);

      const arrow5 = arrowCreate({
        head,
        from: {
          node: right,
          direction: DIRECTION.BOTTOM,
        },
        to: {
          node: right_left,
          direction: DIRECTION.TOP,
        },
      });
      wrapper.appendChild(arrow5.node);

      const arrow6 = arrowCreate({
        head,
        from: {
          node: right,
          direction: DIRECTION.BOTTOM,
        },
        to: {
          node: right_right,
          direction: DIRECTION.TOP,
        },
      });
      wrapper.appendChild(arrow6.node);
    }
  }, []);

  const showLeft = async () => {
    if (data!.user.id == rootNodeId) {
      setOpenModal("left");
    }
  };

  const showRight = async () => {
    if (data!.user.id == rootNodeId) {
      setOpenModal("right");
    }
  };

  const closeModal = () => {
    setOpenModal(null);
  };

  if (!tree) return null;

  return (
    <div
      id="treeWrapper"
      className="w-full bg-gray-900 rounded-[25px] p-4 h-full overflow-auto flex justify-center"
    >
      {/*<Dialog
        isOpen={openModal !== null}
        width={1000}
        closable={true}
        onClose={closeModal}
      >
        <table className="w-full">
          <thead>
            <tr>
              <th>Puntos</th>
              <th>Usuario</th>
              <th>Correo</th>
              <th>Patrocinador</th>
              <th>Fecha</th>
            </tr>
          </thead>
          <tbody>
            {openModal == "left" &&
              leftPoints.map((r, index) => (
                <tr key={index}>
                  <td className="text-center">{r.points}</td>
                  <td className="text-center">{r.user_name}</td>
                  <td className="text-center">{r.user_email}</td>
                  <td className="text-center">{r.user_sponsor}</td>
                  <td className="text-center">{r.created_at}</td>
                </tr>
              ))}
            {openModal == "right" &&
              rightPoints.map((r, index) => (
                <tr key={index}>
                  <td className="text-center">{r.points}</td>
                  <td className="text-center">{r.user_name}</td>
                  <td className="text-center">{r.user_email}</td>
                  <td className="text-center">{r.user_sponsor}</td>
                  <td className="text-center">{r.created_at}</td>
                </tr>
              ))}
          </tbody>
        </table>
      </Dialog>*/}

      <div
        className="relative"
        style={{ width: containerWidth, paddingBottom: 40 }}
      >
        {rootNodeId != data!.user.id && (
          <>
            <div className="absolute left-1 top-1">
              <Button onPress={() => setRootNodeId(data!.user.id)}>
                Inicio
              </Button>
            </div>
            <div className="absolute left-1 top-14">
              <Button
                onPress={() => setRootNodeId(tree.data.parent_binary_user_id)}
              >
                <FaChevronUp />
              </Button>
            </div>
          </>
        )}

        {false && (
          <>
            <div
              className="absolute"
              style={{ left: initPosition - 100, top: 10 }}
            >
              <button onClick={showLeft}>
                <b>Izq: {tree?.data?.left_points} pts</b>
              </button>
            </div>
            <div
              className="absolute"
              style={{ left: initPosition + 320, top: 10 }}
            >
              <button onClick={showRight}>
                <b>Der: {tree?.data?.right_points} pts</b>
              </button>
            </div>
          </>
        )}

        <NodeAvatar
          {...tree.data}
          isSM={isSM}
          id="node-main"
          x={initPosition}
          y={10}
          ref={renderarrow}
        />

        <>
          {tree.left ? (
            <NodeAvatar
              {...tree.left.data}
              isSM={isSM}
              id="left"
              x={initPosition - (isSM ? 200 : 300)}
              y={isSM ? 130 : 220}
              onClick={() => setRootNodeId(tree.left.data.id)}
            />
          ) : (
            <NodeAvatar
              id="left"
              x={initPosition - (isSM ? 200 : 300)}
              y={isSM ? 130 : 220}
              isSM={isSM}
            />
          )}
          {tree.right ? (
            <NodeAvatar
              {...tree.right.data}
              isSM={isSM}
              id="right"
              x={initPosition + (isSM ? 220 : 300)}
              y={isSM ? 130 : 220}
              onClick={() => setRootNodeId(tree.right.data.id)}
            />
          ) : (
            <NodeAvatar
              id="right"
              x={initPosition + (isSM ? 220 : 300)}
              y={isSM ? 130 : 220}
              isSM={isSM}
            />
          )}
        </>

        <>
          {tree.left?.left ? (
            <NodeAvatar
              {...tree.left.left.data}
              isSM={isSM}
              id="left-left"
              x={initPosition - (isSM ? 300 : 430)}
              y={isSM ? 300 : 450}
              size="s"
              onClick={() => setRootNodeId(tree.left.left.data.id)}
            />
          ) : (
            <NodeAvatar
              id="left-left"
              isSM={isSM}
              x={initPosition - (isSM ? 300 : 430)}
              y={isSM ? 300 : 450}
              size="s"
            />
          )}
          {tree.left?.right ? (
            <NodeAvatar
              {...tree.left.right.data}
              isSM={isSM}
              id="left-right"
              x={initPosition - (isSM ? 100 : 160)}
              y={isSM ? 300 : 450}
              size="s"
              onClick={() => setRootNodeId(tree.left.right.data.id)}
            />
          ) : (
            <NodeAvatar
              id="left-right"
              isSM={isSM}
              x={initPosition - (isSM ? 100 : 160)}
              y={isSM ? 300 : 450}
              size="s"
            />
          )}

          {tree.right?.left ? (
            <NodeAvatar
              {...tree.right.left.data}
              isSM={isSM}
              id="right-left"
              x={initPosition + (isSM ? 110 : 170)}
              y={isSM ? 300 : 450}
              size="s"
              onClick={() => setRootNodeId(tree.right.left.data.id)}
            />
          ) : (
            <NodeAvatar
              id="right-left"
              isSM={isSM}
              x={initPosition + (isSM ? 110 : 170)}
              y={isSM ? 300 : 450}
              size="s"
            />
          )}
          {tree.right?.right ? (
            <NodeAvatar
              {...tree.right.right.data}
              isSM={isSM}
              id="right-right"
              x={initPosition + (isSM ? 330 : 450)}
              y={isSM ? 300 : 450}
              size="s"
              onClick={() => setRootNodeId(tree.right.right.data.id)}
            />
          ) : (
            <NodeAvatar
              id="right-right"
              isSM={isSM}
              x={initPosition + (isSM ? 330 : 450)}
              y={isSM ? 300 : 450}
              size="s"
            />
          )}
          <div
            className="absolute"
            style={{
              left: initPosition - (isSM ? 280 : 380),
              top: initPosition + (isSM ? 160 : 120),
            }}
          >
            <Button onPress={() => setRootNodeId(tree.left.data.id)}>
              <FaChevronDown />
            </Button>
          </div>
          <div
            className="absolute"
            style={{
              left: initPosition + (isSM ? 355 : 500),
              top: initPosition + (isSM ? 160 : 120),
            }}
          >
            <Button onPress={() => setRootNodeId(tree.right.data.id)}>
              <FaChevronDown />
            </Button>
          </div>
        </>
      </div>
    </div>
  );
}
